#parallax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background:#a1bfbf;
}

.parallax__layer {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  will-change: transform;
}

.parallax__image {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}